import { Component, OnInit } from '@angular/core';
import {TextEditorComponentConfig} from "@cima/commons";
import {FakeDataService} from "../../services/fake-data.service";


@Component({
  selector: 'reporter-prev-situation',
  templateUrl: './prev-situation.component.html',
  styleUrls: ['./prev-situation.component.scss']
})
export class PrevSituationComponent implements OnInit {

  public sectionEnabled=false

  public textPrev: string = '<span>Testo</span>';
  public config: TextEditorComponentConfig = {height:'200px', placeholder: 'Testo...'}

  /*RAINMAPS*/
  public rainMaps: any = [
    {
      id:0,
      timeRange:''
    }
  ]
  addRainMaps(){
    this.rainMaps = [...this.rainMaps, {id:1, timeRange:''}]
  }
  delRainMaps(item:any) {
    this.rainMaps = this.rainMaps.filter((x: any) => x != item)
  }

  /*WARNINGPLUVIO*/
  public warningsPluvio: any = [
    {
      id:0,
      aggregazione:''
    }
  ]
  addWarningPluvio(){
    this.warningsPluvio = [...this.warningsPluvio, {id:1, aggregazione:''}]
  }
  delWarningPluvio(item:any) {
    this.warningsPluvio = this.warningsPluvio.filter((x: any) => x != item)
  }


  /*WARNINGIDRO*/
  public warningsIdro: any = [
    {
      id:0,
      aggregazione:''
    }
  ]
  addWarningIdro(){
    this.warningsIdro = [...this.warningsIdro, {id:1, aggregazione:''}]
  }
  delWarningIdro(item:any) {
    this.warningsIdro = this.warningsIdro.filter((x: any) => x != item)
  }

  /*UPLOAD FILES*/
  delFile(file:any){
    this.fakeData.uploadedFiles = this.fakeData.uploadedFiles.filter((x: any) => x != file)
  }

  constructor(public fakeData: FakeDataService) { }

  ngOnInit(): void {
  }

}
